*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .clearfix::after{
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden; /* 溢出隐藏属性 */
    visibility: hidden;/* 隐藏属性 */
  }
  .App {
    /* text-align: center; */
    background:url("../../../public/bg.jpg") no-repeat;
    background-size: 100% 100% ;
    height: 100vh;
    border: 1px solid rgba(0, 0, 0, 0);
    width: 100%;
  }
  .App .title{
    width: 300px;
    font-size: 35px;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 50px;
    position: relative;
  }
  .App .title .Vs{
    font-size: 20px;
    position:absolute;
    top: 5px;
  }
  
  .App .main{
    width: 400px;
    background: white;
    margin: 0 auto;
    
  }
  .App .main .main_body{
    width: 320px;
    margin: 0 auto;
    padding-bottom: 30px;
    /* background: red; */
  }
  .App .main .main_header{
    border-bottom: 1px solid black;
    width: 400px;
    /* height: 40px; */
    /* background: red; */
    font-size: 20px;
    font-weight: 100;
  }
  .App .main .main_header .ksdl,.App .main .main_header .kszc{
    line-height: 40px;
  }
  
  .active{
    border-bottom: 3px solid black;
  }
  .App .main .main_header .ksdl:hover,
  .App .main .main_header .kszc:hover
  {
    cursor: pointer;
  }
  .App .main .main_header .ksdl{
    float: left;
    margin-left: 70px;
  }
  .main .main_header .kszc{
    float: right;
    margin-right: 70px;
  }
  label{
    font-size: 16px;
  }
  #login_username,
  #login_password,
  #register_username,
  #register_password1,
  #register_password2,
  #email{
    border: none;
    width: 200px;
    height: 30px;
    margin: 5px 0px;
    border-radius: 2px;
    font-size: 16px;
    padding: 2px 5px;
    border: 1px solid rgb(172, 170, 170);
  }
  #login,#register{
    border: none;
    border-radius: 2px;
    height: 30px;
    width: 160px;
    background: #2795dc;
    font-size: 18px;
    color: white;
    font-weight: 200;
    box-shadow:1px 1px  #000000;
    margin-top: 10px;
  }
  #login:hover,#register:hover{
    cursor: pointer;
  }
  #login:active,#register:active{
    opacity: 0.7;
  }
  .content{
    margin-top: 50px;
    text-align: center;
    /* background: red; */
    position: relative;
  }
  .content .login_username,.content .login_password{
    text-align: center;
    margin-bottom: 10px;
  }
  .content .jzmm{
    text-align: start;
    margin-left: 82px;
  }
  
  .main_body_register{
    display: none;
  }
  .main_body_register .content{
    text-align: end;
  }
  .register_username,
  .register_password1,
  .register_password2,
  .email{
    margin-right: 38px;
    margin-bottom: 10px;
  }
  
  #register{
    margin-right: 80px;
  }
  
  